<template>
  <div>
    <Row style="margin-top:20px">
      <Input
        placeholder="ËØ∑ËæìÂÖ•ÂÖ≥ÈîÆÂ≠óÊåâÂõûËΩ¶ÊàñËÄÖÁÇπÂáªÊêúÁ¥¢Êü•ËØ¢"
        :search="search"
        @on-search="searchRoleName"
        v-model="paginations.username"
        suffix="ios-search"
        style="width: 250px"
      />
      <Button style="margin-left:20px;margin-right:10px" @click="handleAdd" type="success">Êñ∞Â¢ûËßíËâ≤</Button>
      <Button @click="deletTableColum">ÊâπÈáèÂà†Èô§</Button>
      <!-- ÂØπËØùÊ°Ü -->
    </Row>
    <!-- Ë°®Ê†º -->
    <Row style="margin-top:20px">
      <Table
        ref="selection"
        @on-selection-change="(selection) => chekedList = selection"
        :highlight-row="hove"
        :columns="columns1"
        :loading="tableLoading"
        :data="userTable"
        max-height="600"
        height="550"
      ></Table>
    </Row>
    <!-- ÂàÜÈ°µ -->
    <Row>
      <div class="selectStatus">
        <i-button @click="handleSelectAll" style="margin-right:10px">ÂÖ®ÈÄâ</i-button>
      </div>
      <Page
        class="userTablePage"
        :total="totalCount"
        :current.sync="paginations.page"
        @on-change="handleCurrentChange"
        :page-size="paginations.limit"
        :page-size-opts="pageSizeOpts"
        @on-page-size-change="handleSizeChange"
        show-elevator
        show-sizer
      />
    </Row>
    <roleModal @add="getRoleList()" @update="getRoleList()" :formData="formData" :dialog="dialog"></roleModal>
  </div>
</template>

<script>
import { RoleList } from "@/api/roles/roleList";
import { deletRole } from "@/api/roles/deletRole";
import roleModal from "@/components/roleModal";
export default {
  data() {
    return {
      formData: {
        roleId: 0,
        roleName: "",
        remark: "",
        menuIdList: []
      },
      dialog: {
        value: false,
        title: "",
        option: ""
      },
      paginations: {
        //ÈªòËÆ§Êü•ËØ¢Êù°‰ª∂
        page: 1, //ÂΩìÂâçÈ°µÁ†Å
        limit: 10, //ÊØèÈ°µÊù°Êï∞
        sidx: "", //ÊéíÂ∫èÂ≠óÊÆµ
        order: "asc", //ÊéíÂ∫èÊñπÂºèÂ¶ÇÔºöasc„ÄÅdesc
        rolename: "" //Áî®Êà∑ÂêçÊü•ËØ¢
      },
      userTable: [], //Ë°®Ê†ºÊï∞ÊçÆ
      columns1: [
        { type: "selection", width: 50, minWidth: 100 },
        {
          title: "id",
          key: "roleId",
          align: "center",
          sortable: true,
          minWidth: 100
        },
        {
          title: "ËßíËâ≤ÂêçÁß∞",
          key: "roleName",
          align: "center",
          sortable: true,
          minWidth: 100
        },
        { title: "Â§áÊ≥®", key: "remark", align: "center", minWidth: 100 },
        {
          title: "ÂàõÂª∫Êó∂Èó¥",
          key: "createTime",
          sortable: true,
          minWidth: 100,
          align: "center"
        },
        {
          title: "Êìç‰Ωú",
          align: "center",
          fixed: "right",
          width: 150,
          minWidth: 100,
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: { size: "small", type: "text" },
                  on: {
                    click: () => {
                      this.editRole(params);
                    }
                  },
                  style: { marginRight: "5px", color: "#39d639e8" }
                },
                "ÁºñËæë"
              ),
              h(
                "Button",
                {
                  props: { size: "small", type: "text" },
                  style: { color: "#39d639e8" },
                  on: {
                    click: () => {
                      // console.log(params);
                      this.deletOneRole(params);
                    }
                  }
                },
                "Âà†Èô§"
              )
            ]);
          }
        }
      ],
      paginations: {
        //ÈªòËÆ§Êü•ËØ¢Êù°‰ª∂
        page: 1, //ÂΩìÂâçÈ°µÁ†Å
        limit: 10, //ÊØèÈ°µÊù°Êï∞
        sidx: "", //ÊéíÂ∫èÂ≠óÊÆµ
        order: "desc", //ÊéíÂ∫èÊñπÂºèÂ¶ÇÔºöasc„ÄÅdesc
        roleName: "" //Áî®Êà∑ÂêçÊü•ËØ¢
      },
      totalCount: 0, //Êï∞ÊçÆÊÄªÊï∞
      totalPage: 1, //ÂÖ±Âá†È°µ
      pageSizeOpts: [10, 15, 20, 25], //ÈÄâÊã©ÊØèÈ°µÊòæÁ§∫Â§öÂ∞ëÊù°
      hove: true, //tableÊòØÂê¶È´ò‰∫Æ
      tableLoading: false, //Âä†ËΩΩlË°®Ê†ºoading
      search: true, //ÂºÄÂêØsearchËæìÂÖ•Ê°Ü
      SelectAll: false, //ÈªòËÆ§‰∏çÊòØÂÖ®ÈÄâ
      chekedList: [] //tableÈÄâ‰∏≠ÁöÑcolum
    };
  },
  components: { roleModal },
  methods: {
    async getRoleList() {
      //Ëé∑ÂèñËßíËâ≤ÂàóË°®
      this.tableLoading = true;
      const res = await RoleList(this.paginations);
      const { code, msg, page } = res.data;
      if (code == 0) {
        // console.log(page);
        const { list, pageSize, totalCount, totalPage, currPage } = page;
        this.userTable = list; //Ëé∑ÂèñÁöÑÊï∞ÊçÆÁªôË°®Ê†º
        this.paginations.limit = pageSize; //ÊØèÈ°µÊù°Êï∞
        this.totalPage = totalPage; //ÂÖ±Âá†È°µ
        this.totalCount = totalCount; //ÊÄªÈ°µÊï∞
        this.paginations.page = currPage; //ÂΩìÂâçÈ°µ
      } else if (code == 500) {
        this.message.error(msg);
      }
      this.tableLoading = false;
    },
    editRole(params) {
      //ÁºñËæë‰øÆÊîπuser
      // console.log(params.row);
      this.dialog = {
        value: true,
        title: "ËßíËâ≤",
        option: "‰øÆÊîπ"
      };
      const { roleId, roleName, remark } = params.row;
      this.formData.roleId = roleId;
      this.formData.roleName = roleName;
      this.formData.remark = remark;
    },
    deletOneRole(params) {
      console.log(params);
      const { roleName, roleId } = params.row;
      this.$Modal.confirm({
        title: "ÊèêÁ§∫",
        content: `ÊÇ®Ê≠£Âú®Âà†Èô§Áî®Êà∑<span style='color:#42be19'>${roleName}</span>,ÊòØÂê¶Á°ÆÂÆöË¶ÅÂà†Èô§üòû`,
        loading: true,
        onOk: () => {
          deletRole([roleId]).then(({ data }) => {
            if (data.code === 0) {
              this.$Message.success({
                content: `Áî®Êà∑<span style='color:#42be19'>${roleName}</span>Â∑≤Âà†Èô§`
              });
              this.getRoleList();
            } else {
              this.$Message.error({
                content: `Áî®Êà∑<span style='color:red'>${roleName}</span>Âà†Èô§Â§±Ë¥•`
              });
            }
          });
          this.$Modal.remove();
        }
      });
    },
    handleCurrentChange() {
      //È°µÁ†ÅÊîπÂèòÊó∂Ëß¶ÂèëÈáçÊñ∞ËØ∑Ê±ÇÊï∞ÊçÆ
      this.getRoleList();
    },
    handleSizeChange(page) {
      this.paginations.limit = page; //ÈÄâÊã©ÊØèÈ°µÁîüÊàêÂá†Êù°Êï∞ÊçÆÂêéÊÇîËß¶ÂèëËøô‰∏™ÂáΩÊï∞ ËøîÂõûÂÄºÊòØÈÄâÊã©ÁöÑÊòæÁ§∫Êù°Êï∞
      this.getRoleList();
    },
    handleSelectAll() {
      this.$refs.selection.selectAll((this.SelectAll = !this.SelectAll)); //ÊéßÂà∂ÊòØÂê¶ÂÖ®ÈÄâ
      console.log(this.chekedList);
    },
    deletTableColum() {
      //ÊâπÈáèÂà†Èô§
      const deletTableList = this.chekedList.map(item => item.roleId);
      if (deletTableList.length === 0) {
        return this.$Modal.error({
          title: "Êó†ÊïàÁöÑÊìç‰Ωú",
          content: "ËØ∑ÈÄâÊã©Ë¶ÅÂà†Èô§ÁöÑÂØπË±°"
        });
      }
      this.$Modal.confirm({
        title: "ÂèãÊÉÖÊèêÁ§∫",
        content: "ÊÇ®Ê≠£Âú®ÂØπÊï∞ÊçÆËøõË°åÊâπÈáèÂà†Èô§,ÊòØÂê¶Ë¶ÅÁªßÁª≠Êìç‰ΩúÔºü‚óî ‚Ä∏‚óî?",
        loading: true,
        cancelText: "ÁÇπÈîô‰∫Ü",
        onOk: () => {
          deletRole(deletTableList).then(({ data }) => {
            console.log(data);
            if (data.code === 0) {
              this.$Message.success({
                content: "ÊâπÈáèÂà†Èô§ÊàêÂäüÂï¶"
              });

              this.getRoleList();
            } else {
              this.$Message.error(data.msg);
            }
            this.chekedList = [];
          });
          this.$Modal.remove();
        }
      });
    },
    searchRoleName(val) {
      this.paginations.roleName = val;
      this.getRoleList();
    },
    handleAdd() {
      //Êñ∞Â¢ûËßíËâ≤
      this.dialog = {
        value: true,
        title: "ËßíËâ≤",
        option: "Ê∑ªÂä†"
      };
    }
  },
  created() {
    this.getRoleList();
  }
};
</script>

<style scoped>
.selectStatus {
  float: left;
  margin-top: 10px;
}
.userTablePage {
  margin-top: 10px;
  float: right;
}
</style>